.containerAbout {
    max-width: 1920px;
    width: 100%;

    
    align-items: center;
    display: flex;
    flex-direction: column;
}

.about {
    display: flex;
    justify-content: center;
    margin: 25px 0;;
}

.aboutArticles {
    display: flex;
    max-width: 1200px;
    justify-content: center;
    
}

.aboutArticles div {
    
    display: flex;
    flex-direction: column;
    padding: 0 20px;
}

.aboutArticles div p {
    
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
   
    margin-bottom: 30px;
    
    letter-spacing: 0.6px;
    text-align: start;
    color: #242121;
    margin-top: 0;
}