.container {
    background-image: url(../assets/img/haeder_background.png);
    max-width: 1920px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0;
}

.logoAndNavigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 35px;
}

.logo {
    width: 25%;
}

.logo img {
    width: 60px;
    height: 63px;
}

nav {
    width: 50%;
    display: flex;
    justify-content: space-around;
}

a {
    text-decoration: none;
    padding-bottom: 5px;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.6px;
    color: #FFFFFF;

}

nav a:hover {
    color: #00B2A0;
    border-bottom: 1px solid #00B2A0;
}

.tail {
    width: 25%;
}

.wawe {
    display: flex;
    flex-direction: column;
    height: fit-content;
}

.wawe p {
    margin: 25px auto 0px;
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 700;
    font-size: 100px;
    letter-spacing: 0.6px;
    color: #0D302C;
}

.wawe h6 {
    margin: 0px auto 25px;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.6px;
    color: #000000;
}

@media screen and (max-width: 900px) {
   
    .wawe p {
        margin-top: 0px;
        height: 70px;
        font-size: 70px;
    }

    .logoAndNavigation {
        flex-direction: column;
        padding: 0;
    }

    nav {
        flex-direction: column;
    }
}

@media screen and (max-width: 720px) {
   
    .wawe {
        display: none;
    }

}

@media screen and (max-width: 480px) {
   
    .logo img {
        width: 30px;
        height: 32px;
    }

    a {
        font-size: 16px;
        padding: 0;
    }

}