.containerGetThePeaks {
    display: flex;
    justify-content: space-around;
}

.getThePeaks {
    display: flex;
    justify-content: center;
}

.getThePeaksAndP {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.descriptions {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.descriptions p {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #242121;
}

.img {
    width: 480px;
    height: 425px;
    margin: 25px 25px;
}

@media screen and (max-width: 1000px) {
    .containerGetThePeaks {
        flex-direction: column;
        align-items: center;
    }
}