.containerContacts {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 25px 0;

}

.contacts {
    display: flex;
    justify-content: center;
    margin: 25px 0;
    ;
}

.formAndContacts {
    display: flex;
    justify-content: center;

}

form {
    max-width: 350px;
    width: 100%;
    margin: 0 20px;
    min-height: 300px;
    display: flex;
    flex-direction: column;
}

input {
    min-width: 200px;
    width: 100%;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #00B2A0;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px solid #01353E;
}

textarea {

    width: 100%;
    height: 130px;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #00B2A0;
    margin-bottom: 20px;
    resize: none;
    border: none;
    border-bottom: 1px solid #01353E;
}

button {
    max-width: 175px;
    width: 100%;

    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #00B2A0;

    margin-bottom: 0;
}

.ps {
    max-width: 350px;
    min-height: 300px;
    background-color: #F8F8F8;
}

.ps p {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #242121;
    padding: 0 0 0 20px;
}

.links {
    display: flex;
    justify-content: center;
    margin: 50px 0;
}

.links a {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 18px;
text-decoration: none;
    letter-spacing: 0.1px;
    margin-right: 10px;
    color: #242121;
}
.links a:hover {
    color: #00B2A0;

}

@media screen and (max-width: 600px) {
    .formAndContacts {
        flex-direction: column;
        align-items: center;
    }
}