.price {
    display: flex;
    justify-content: center;
    margin: 25px 0;
}

.priceBloks {
    display: flex;
    justify-content: center;
}

.priceBloks div {
    width: 360px;
}

.priceBloks div h6 {
    margin: 45px 0 50px;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 40px;
    color: #01353E;
}

.priceBloks div ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.priceBloks div ul li {
    list-style: none;
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.6px;
    color: #242121;
    margin: 10px 0;
}

@media screen and (max-width: 670px) {
    .priceBloks {
        flex-direction: column;
        align-items: center;
    }
}