.containerYouWillGet {
    max-width: 1920px;
    width: 100%;
    
    display: flex;
    flex-direction: column;
    align-items: center;
}

.youWillGet {
    display: flex;
    justify-content: center;
    margin: 25px 0 25px;
}


.offers {
    max-width: 1200px;
    display: flex;
    
}

.offers div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.offers div img {
    width: 220px;
    height: 50px;
}

.offers div p {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    /* width: 350px; */
    padding: 0 25px;
    text-align: start;
    letter-spacing: 0.6px;
display: flex;


    color: #242121;
}

.graphicsAndP {
    max-width: 1200px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 25px 0;
    padding: 0 20px;
}

.graphics {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 25px;
    padding-left: 25px;
}

.pargraphs {
    max-width: 600px;
    margin-bottom: 30px;
    padding: 0 70px;
}

.pargraphs p {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    text-align: start;
    letter-spacing: 0.6px;
    color: #242121;
}

@media screen and (max-width: 680px) {
    .offers {
        flex-direction: column;
    }
    .graphicsAndP {
        flex-direction: column;
    }
}